<template>
  <v-container>
    <v-layout row wrap justify-space-between>
      <v-flex pa-2 xs12>
        <v-card>
          <v-card-title class="pt-1">Spoonacular API Key</v-card-title>
          <v-card-subtitle class="pb-1">Enter your Spoonacular API key</v-card-subtitle>
          <v-card-text class="pb-2">
            <v-text-field v-model="spoonApiKey" dense hide-details placeholder="API key" outlined />
            <v-checkbox
              v-model="rememberSpoonApiKey"
              label="Remember api key (specific to this machine and browser)"
              class="mt-0 mb-1"
              hide-details
              color="primary"
            ></v-checkbox>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex pa-2 xs12>
        <v-card>
          <v-card-title class="pt-1">Edamam API Keys</v-card-title>
          <v-card-subtitle class="pb-1">Enter your Edamam App ID</v-card-subtitle>
          <v-card-text class="pb-2">
            <v-text-field v-model="edamamAppId" dense hide-details placeholder="App ID" outlined />
          </v-card-text>
          <v-card-subtitle class="pb-1 pt-0">Enter your Edamam App Key</v-card-subtitle>
          <v-card-text class="pb-2">
            <v-text-field v-model="edamamAppKey" dense hide-details placeholder="App Key" outlined />
          </v-card-text>
          <v-card-text class="pb-2 pt-0">
            <v-checkbox
              v-model="rememberEdamamKeys"
              label="Remember api keys (specific to this machine and browser)"
              class="mt-0 mb-1"
              hide-details
              color="primary"
            ></v-checkbox>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex pa-2 xs12>
        <v-card>
          <v-card-title class="pt-1">Recipe URL</v-card-title>
          <v-card-subtitle class="pb-1">Enter the url of the recipe to import</v-card-subtitle>
          <v-card-text class="pb-2">
            <v-text-field v-model="recipeUrl" dense hide-details placeholder="API key" outlined />
            <v-checkbox
              v-model="forceReimport"
              label="Force re-import"
              class="mt-0 mb-1"
              hide-details
              color="primary"
            ></v-checkbox>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex pa-2 xs12>
        <v-btn
          x-large
          color="success"
          :disabled="searching || !spoonApiKey || !recipeUrl"
          :loading="searching"
          @click="runImport(recipeUrl, forceReimport)"
          >Import</v-btn
        >
      </v-flex>

      <SearchResults />
    </v-layout>
  </v-container>
</template>

<script>
import SearchResults from "./SearchResults";
import { mapState, mapActions } from "vuex";

export default {
  name: "ImportForm",

  components: {
    SearchResults
  },

  methods: {
    ...mapActions(["runImport"])
  },

  computed: {
    ...mapState(["searching"]),
    spoonApiKey: {
      get() {
        return this.$store.state.spoonApiKey;
      },
      set(value) {
        this.$store.commit({
          type: "setSpoonApiKey",
          spoonApiKey: value
        });
      }
    },
    rememberSpoonApiKey: {
      get() {
        return this.$store.state.rememberSpoonApiKey;
      },
      set(value) {
        this.$store.commit({
          type: "setRememberSpoonApiKey",
          rememberSpoonApiKey: value
        });
      }
    },
    edamamAppId: {
      get() {
        return this.$store.state.edamamAppId;
      },
      set(value) {
        this.$store.commit({
          type: "setEdamamAppId",
          edamamAppId: value
        });
      }
    },
    edamamAppKey: {
      get() {
        return this.$store.state.edamamAppKey;
      },
      set(value) {
        this.$store.commit({
          type: "setEdamamAppKey",
          edamamAppKey: value
        });
      }
    },
    rememberEdamamKeys: {
      get() {
        return this.$store.state.rememberEdamamKeys;
      },
      set(value) {
        this.$store.commit({
          type: "setRememberEdamamKeys",
          rememberEdamamKeys: value
        });
      }
    }
  },

  data: () => ({
    recipeUrl: "",
    forceReimport: false
  })
};
</script>
