import Vue from "vue";
import Vuex from "vuex";
import { createHash } from "crypto";
import VuexPersistence from "vuex-persist";

import * as constants from "./constants";

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: state => ({
    ...(state.rememberSpoonApiKey && { spoonApiKey: state.spoonApiKey }),
    ...{ rememberSpoonApiKey: state.rememberSpoonApiKey },
    ...(state.rememberEdamamKeys && { edamamAppId: state.edamamAppId, edamamAppKey: state.edamamAppKey }),
    ...{ rememberEdamamKeys: state.rememberEdamamKeys }
  }),
  filter: mutation =>
    mutation.type == "setSpoonApiKey" ||
    mutation.type == "setRememberSpoonApiKey" ||
    mutation.type == "setEdamamAppId" ||
    mutation.type == "setEdamamAppKey" ||
    mutation.type == "setRememberEdamamKeys"
});

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== "production",
  plugins: [vuexLocal.plugin],
  state: {
    spoonApiKey: null,
    rememberSpoonApiKey: false,
    edamamAppId: null,
    edamamAppKey: null,
    rememberEdamamKeys: false,
    diets: null,
    cuisines: null,
    intolerances: null,
    mealTypes: null,
    ingredients: null,
    sort: "default",
    sortDirection: "asc",
    searching: false,
    resultsPerPage: 10,
    lastSearchUrl: null,
    lastResults: null,
    lastOffset: 0,
    lastTotalResults: 0,
    lastResultsPage: 1,
    menuItems: {},
    nutritionCache: {}
  },
  getters: {
    dietOptions: () => {
      return constants.dietsList;
    },
    cuisineOptions: () => {
      return constants.cuisineList;
    },
    intoleranceOptions: () => {
      return constants.intoleranceList;
    },
    mealOptions: () => {
      return constants.mealTypeList;
    },
    sortOptions: () => {
      return constants.sortTypeList;
    },
    lastResultsPageCount: state => {
      return Math.ceil(state.lastTotalResults / state.resultsPerPage);
    },
    menuItemsCount: state => {
      return Object.keys(state.menuItems).length;
    }
  },
  mutations: {
    setSpoonApiKey(state, payload) {
      state.spoonApiKey = payload.spoonApiKey;
    },
    setRememberSpoonApiKey(state, payload) {
      state.rememberSpoonApiKey = payload.rememberSpoonApiKey;
    },
    setEdamamAppId(state, payload) {
      state.edamamAppId = payload.edamamAppId;
    },
    setEdamamAppKey(state, payload) {
      state.edamamAppKey = payload.edamamAppKey;
    },
    setRememberEdamamKeys(state, payload) {
      state.rememberEdamamKeys = payload.rememberEdamamKeys;
    },
    setDiets(state, payload) {
      state.diets = payload.selectedOptions;
    },
    setCuisines(state, payload) {
      state.cuisines = payload.selectedOptions;
    },
    setIntolerances(state, payload) {
      state.intolerances = payload.selectedOptions;
    },
    setMeals(state, payload) {
      state.mealTypes = payload.selectedOptions;
    },
    setIngredients(state, payload) {
      state.ingredients = payload.selectedOptions;
    },
    setSorting(state, payload) {
      state.sort = payload.sort === "default" ? null : payload.sort;
      state.sortDirection =
        payload.sortDirection === "asc" || payload.sortDirection === "desc" ? payload.sortDirection : "asc";
    },
    toggleSearching(state) {
      state.searching = !state.searching;
    },
    setLastSearchUrl(state, payload) {
      state.lastSearchUrl = payload.url;
    },
    setLastSearchResults(state, payload) {
      state.lastTotalResults = payload.totalResults;
      state.lastResults = payload.results;
      state.lastOffset = payload.offset;
    },
    setLastResultsPage(state, pageNumber) {
      state.lastResultsPage = pageNumber;
    },
    addMenuItem(state, payload) {
      Vue.set(state.menuItems, payload.item.hashedUrl, payload.item);
    },
    removeMenuItem(state, payload) {
      Vue.delete(state.menuItems, payload.item.hashedUrl);
    },
    clearMenuItems(state) {
      state.menuItems = {};
    },
    addNutritionItem(state, payload) {
      Vue.set(state.nutritionCache, payload.item.hashedUrl, payload.item);
    }
  },
  actions: {
    runQuery: async ({ commit, state }, pageNumber = 1) => {
      commit("toggleSearching");

      // build the full search url
      let fullUrl = `${constants.apiSearchUrl}?apiKey=${state.spoonApiKey}&addRecipeInformation=true&fillIngredients=true`;
      if (state.diets && state.diets.length > 0) {
        fullUrl += `&diet=${state.diets.join(",")}`;
      }
      if (state.cuisines && state.cuisines.length > 0) {
        fullUrl += `&cuisine=${state.cuisines.join(",")}`;
      }
      if (state.intolerances && state.intolerances.length > 0) {
        fullUrl += `&intolerances=${state.intolerances.join(",")}`;
      }
      if (state.mealTypes && state.mealTypes.length > 0) {
        fullUrl += `&type=${state.mealTypes.join(",")}`;
      }
      if (state.ingredients && state.ingredients.length > 0) {
        fullUrl += `&includeIngredients=${state.ingredients.join(",")}`;
      }
      fullUrl += `&sort=${state.sort}&sortDirection=${state.sortDirection}`;

      // update the last search url so we can have it around if the form changes
      commit({
        type: "setLastSearchUrl",
        url: encodeURI(fullUrl)
      });

      // add the offset for page number
      fullUrl += `&offset=${(pageNumber - 1) * state.resultsPerPage}`;

      // fetch some data!
      const response = await fetch(fullUrl, {
        method: "GET",
        mode: "cors",
        cache: "no-cache"
      });

      if (response.ok) {
        const responseData = await response.json(); // parses JSON response into native JavaScript objects

        responseData.results.forEach(
          item =>
            (item["hashedUrl"] = createHash("sha1")
              .update(item.sourceUrl)
              .digest("hex"))
        );

        commit({
          type: "setLastSearchResults",
          offset: responseData.offset,
          results: responseData.results,
          // for some reason the spoonacular api stops at an offset of 900, and has max 10 items per page,
          // so 910 is the max results we can ever get to
          totalResults: responseData.totalResults <= 910 ? responseData.totalResults : 910
        });
        commit("setLastResultsPage", pageNumber);
      } else {
        // do something if it wasn't ok
        commit({
          type: "setLastSearchResults",
          offset: 0,
          results: null,
          totalResults: 0
        });
        commit("setLastResultsPage", 1);
      }

      commit("toggleSearching");
    },
    getNutrition: async ({ commit, state }, recipe, forceReload = false) => {
      // make sure we don't already have it
      if (state.nutritionCache[recipe.hashedUrl] && !forceReload) {
        return;
      }

      // fetch it
      // build the full url
      let fullUrl = `${constants.apiNutritionUrl}?app_id=${state.edamamAppId}&app_key=${state.edamamAppKey}`;

      // build the json object that we'll post
      let postObject = {
        title: recipe.title,
        yield: recipe.servings,
        ingr: [
          ...(recipe.usedIngredients != undefined && recipe.usedIngredients),
          ...(recipe.missedIngredients != undefined && recipe.missedIngredients)
        ].map(i => i.original)
      };

      // fetch some data!
      const response = await fetch(fullUrl, {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        body: JSON.stringify(postObject),
        headers: {
          "Content-Type": "application/json"
        }
      });

      if (response.ok) {
        const responseData = await response.json(); // parses JSON response into native JavaScript objects

        // convert the response to a better format
        let nutrients = [];
        for (const nutrient in responseData.totalNutrients) {
          if (responseData.totalNutrients.hasOwnProperty(nutrient)) {
            const item = responseData.totalNutrients[nutrient];

            // make sure we have DV data for it
            nutrients.push({
              title: cleanupNutritionLabels(item.label),
              amount: (item.quantity / recipe.servings).toFixed(3),
              unit: item.unit,
              percentDV: responseData.totalDaily[nutrient]
                ? (responseData.totalDaily[nutrient].quantity / recipe.servings).toFixed(3)
                : null
            });
          }
        }

        commit({
          type: "addNutritionItem",
          item: {
            hashedUrl: recipe.hashedUrl,
            nutrients: nutrients
          }
        });
      } else {
        commit({
          type: "addNutritionItem",
          item: {
            hashedUrl: recipe.hashedUrl,
            failure: true
          }
        });
      }
    },
    runImport: async ({ commit, state }, url, forceReimport = false) => {
      commit("toggleSearching");

      // build the full import url
      let fullUrl = `${constants.apiImportUrl}?apiKey=${state.spoonApiKey}&url=${url}&forceExtraction=${forceReimport}&includeNutrition=true`;

      // fetch some data!
      const response = await fetch(fullUrl, {
        method: "GET",
        mode: "cors",
        cache: "no-cache"
      });

      if (response.ok) {
        const responseData = await response.json(); // parses JSON response into native JavaScript objects

        // ingredients come through weird so clean that up
        responseData["usedIngredients"] = responseData.extendedIngredients.map(item => ({
          original: item.original,
          name: item.name
        }));
        responseData["missedIngredients"] = []; // easier to do this than deal with the problems :P
        delete responseData.extendedIngredients;

        responseData["hashedUrl"] = createHash("sha1")
          .update(responseData.sourceUrl)
          .digest("hex");

        commit({
          type: "setLastSearchResults",
          offset: 0,
          results: [responseData],
          // for some reason the spoonacular api stops at an offset of 900, and has max 10 items per page,
          // so 910 is the max results we can ever get to
          totalResults: 1
        });
        commit("setLastResultsPage", 1);
      }

      commit("toggleSearching");
    }
  },
  modules: {}
});

function cleanupNutritionLabels(label) {
  switch (label) {
    case "Energy":
      return "Calories";
    case "Saturated":
      return "Saturated Fat";
    case "Trans":
      return "Trans Fat";
    default:
      return label;
  }
}
